<template>
  <section>
    <div class="container">
      <div class="border p-3">
        <div class="row">
          <!-- +edit -->
          <div class="col-sm-3">
            <div class="avatar pb-3">
              <img :src="user._links.avatar" alt class="rounded img-fluid w-100" />
            </div>

            <router-link
              :to="{name:'SettingProfile'}"
              v-if="sharestate.user_id == $route.params.id"
            >
              <button type="button" class="btn btn-outline-primary w-100 mb-2">Settings</button>
            </router-link>
            <button
              type="button"
              class="btn btn-outline-danger w-100 mb-5"
              v-if="sharestate.user_id == $route.params.id"
            >Delete Account</button>
            <button
              type="button"
              class="btn btn-outline-primary w-100 mb-5"
              v-if="sharestate.user_id != $route.params.id && user.is_following"
              @click="onUnfollowUser($route.params.id)"
            ></button>
            <button
              type="button"
              class="btn btn-outline-primary w-100 mb-5"
              v-else-if="sharestate.user_id != $route.params.id && !user.is_following"
              @click="onFollowUser($route.params.id)"
            ></button>

            <!-- <router-link :to="{name:'EditProfile',params:{id:sharestate.user_id}}" v-if="sharestate.user_id == $route.params.id">
                        <button type="button" class="btn btn-outline-primary w-100 mb-5">Edit profile</button>
                    </router-link>
            <button v-else type="button" class="btn btn-outline-secondary w-100 mb-5 disabled custom-btn">Edit profile</button>-->
          </div>

          <!--  -->
          <div class="col-sm-9">
            <ul class="nav nav-tabs">
              <li class="nav-item">
                <!-- 
                                v-bind:active-class
                                tabclassactivevue-routeractive-class CSS 
                                tabaurlurltab
                -->
                <router-link
                  :to="{name:'UserOverview'}"
                  v-bind:active-class="'active'"
                  class="nav-link"
                  href="#"
                  v-bind:class="isUserOverView"
                ></router-link>
              </li>
              <li class="nav-item">
                <router-link
                  :to="{name:'UserFollowers'}"
                  v-bind:active-class="'active'"
                  class="nav-link"
                  href="#"
                >
                  
                  <span class="badge badge-pill badge-danger ml-1">{{user.followers_count}}</span>
                </router-link>
              </li>
              <li class="nav-item">
                <router-link
                  :to="{name:'UserFollowing'}"
                  v-bind:active-class="'active'"
                  class="nav-link"
                  href="#"
                >
                  
                  <span class="badge badge-pill badge-danger ml-1">{{user.followeds_count}}</span>
                </router-link>
              </li>

              <li class="nav-item">
                <router-link
                  :to="{name:'Posts'}"
                  class="nav-link"
                  href="#"
                  v-bind:active-class="'active'"
                >
                  
                  <span class="badge badge-pill badge-danger ml-1">{{user.posts_count}}</span>
                </router-link>
              </li>
              <li class="nav-item">
                <a class="nav-link" href="#" v-bind:active-class="'active'">
                  
                  <span class="badge badge-pill badge-danger ml-1">{{user.followed_posts_count}}</span>
                </a>
              </li>
            </ul>
            <router-view></router-view>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import store from "../../store.js";
export default {
  data() {
    return {
      sharestate: store.state,
      user: {
        id: "",
        email: "",
        username: "",
        name: "",
        location: "",
        about_me: "",
        member_since: "",
        last_seen: "",
        followeds_count: 0,
        followers_count: 0,
        posts_count: 0,
        followed_posts_count: 0,
        _links: {
          self: "",
          avatar: ""
        }
      }
    };
  },
  computed: {
    isUserOverView: function() {
      const tabs = [
        "UserFollowers",
        "UserFollowing",
        "UserPostsList",
        "UserFollowedsPostsList"
      ];
      if (tabs.indexOf(this.$route.name) == -1) {
        return "active";
      } else {
        return "";
      }
    }
  },
  methods: {
    onFollowUser(id) {
      const path = `/follow/${id}`;
      this.$axios
        .get(path)
        .then(res => {
          console.log("ok");
          this.getUser(id);
        })
        .catch(e => {
          console.error(e);
        });
    },
    onUnfollowUser(id) {
      const path = `/unfollow/${id}`;
      this.$axios
        .get(path)
        .then(res => {
          console.log("ok");
          this.getUser(id);
        })
        .catch(e => {
          console.error(e);
        });
    },
    getUser(userid) {
      //userid404
      //  axiosresponse404route.back()
      //Profileroute.back()?/user/0
      //  1.Profile/user/0/user/0
      //  2.store.jsuserid0
      //  ....Profile/user/0
      //  ....created

      this.$axios
        .get(`/users/${userid}`)
        .then(res => {
          console.log(res);
          this.user = res.data;
        })
        .catch(e => {
          console.log(e);
        });
    }
  },
  created: function() {
    //userid
    //  1.this.sharestate.user_id ,
    //  2.this.$route.params.id
    let userid = this.$route.params.id;
    if (userid == "0") {
      userid = this.sharestate.user_id;
      this.$router.replace(`/user/${userid}`);
    }
    this.getUser(userid);
  },
  beforeRouteUpdate(to, from, next) {
    next();
    this.getUser(to.params.id);
  }
};
</script>

<style scoped>
/*
    disabled
 */
.custom-btn {
  cursor: not-allowed;
}
.custom-btn:focus {
  -webkit-box-shadow: 0 0 0 0;
  box-shadow: 0 0 0 0;
}
</style>


